import React, { useState, useEffect } from "react";
// Styles
import styled from "styled-components";
import {
  Container,
  Card,
  Form,
  Table,
  Button,
  Row,
  Col,
} from "react-bootstrap";
import { yupResolver } from "@hookform/resolvers/yup";
import * as yup from "yup";
import { useForm } from "react-hook-form";
import { Icon } from "@iconify/react";
const StyledAddButton = styled.button`
  width: 10rem;
  border: 2px solid #555;
  border-radius: 20px;
  color: #555;
  text-decoration: none;
  &:hover {
    background-color: #a9fecd;
  }
`;
const GPAApp = () => {
  const schema = yup.object().shape({
    name: yup.string().notRequired(),
    grade: yup
      .string()
      .oneOf(
        ["A+", "A", "B+", "B", "C+", "C", "D+", "D", "F"],
        "Vui lng chn im"
      )
      .required("Vui lng chn im"),
    credits: yup
      .number()
      .typeError("Hng phi l s")
      .transform((value, originalValue) =>
        originalValue === "" ? undefined : value
      )
      .required("Vui lng nhp s tn ch"),
  });
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm({ resolver: yupResolver(schema) });
  const [courseList, setCourseList] = useState([]);
  const [cntCourse, setCntCourse] = useState(0);
  const [gpa, setGpa] = useState("");

  const [showCourseTable, setShowCourseTable] = useState(false);

  useEffect(() => {
    setShowCourseTable(courseList.length === 0 ? false : true);
  }, [courseList]);

  const addCourse = (data) => {
    console.log(data);
    setCntCourse(cntCourse + 1);
    const newCourseName = data.name || `Mn ${cntCourse + 1}`;
    setCourseList([
      ...courseList,
      {
        name: newCourseName,
        credits: parseInt(data.credits),
        grade: data.grade,
      },
    ]);
    reset();
  };

  const deleteCourse = (index) => {
    const newCourseList = courseList.filter((_, i) => i !== index);
    setCourseList(newCourseList);
  };

  const getScale4Grade = (grade) => {
    if (grade === "A+") return 4;
    if (grade === "A") return 3.7;
    if (grade === "B+") return 3.5;
    if (grade === "B") return 3;
    if (grade === "C+") return 2.5;
    if (grade === "C") return 2;
    if (grade === "D+") return 1.5;
    if (grade === "D") return 1;
    return 0;
  };
  useEffect(() => {
    if (courseList.length === 0) {
      setGpa("");
      return;
    }
    let totalPoints = 0;
    let totalCredits = 0;
    courseList.forEach((course) => {
      totalCredits += course.credits;
      totalPoints += course.credits * getScale4Grade(course.grade);
    });
    const calculatedGpa = totalPoints / totalCredits;
    setGpa(`GPA: ${Math.round(calculatedGpa * 100) / 100}`);
  }, [courseList]);

  const grades = [
    { value: "A+", label: "A+" },
    { value: "A", label: "A" },
    { value: "B+", label: "B+" },
    { value: "B", label: "B" },
    { value: "C+", label: "C+" },
    { value: "C", label: "C" },
    { value: "D+", label: "D+" },
    { value: "D", label: "D" },
    { value: "F", label: "F" },
  ];

  const credits = [
    { value: 1, label: "1" },
    { value: 2, label: "2" },
    { value: 3, label: "3" },
    { value: 4, label: "4" },
  ];

  return (
    <>
      <main>
        <Container className="d-flex justify-content-center"></Container>
        <Container className="my-5">
          <div className="col col-md-6 col-sm-12 mx-auto">
            <Card className="p-3 shadow">
              <Card.Title className="text-center">Tnh GPA</Card.Title>
              <Form className="mt-2" onSubmit={handleSubmit(addCourse)}>
                <Row>
                  <Col xs={12} lg={4}>
                    <Form.Group className="mb-3" controlId="formCourseName">
                      <Form.Label className="fw-bold">Mn hc</Form.Label>
                      <Form.Control
                        className="border border-2"
                        type="text"
                        placeholder="Nhp tn mn hc"
                        {...register("name")}
                      />
                      {errors.name && (
                        <Form.Text className="text-danger">
                          {errors.name.message}
                        </Form.Text>
                      )}
                    </Form.Group>
                  </Col>
                  <Col xs={12} lg={4}>
                    <Form.Group className="mb-3" controlId="formCourseCredit">
                      <Form.Label className="fw-bold">S tn ch(*)</Form.Label>
                      <Form.Select
                        className="border border-2"
                        {...register("credits")}
                      >
                        <option value="">Chn s tn ch</option>
                        {credits.map((credit) => (
                          <option key={credit.value} value={credit.value}>
                            {credit.label}
                          </option>
                        ))}
                      </Form.Select>
                      {errors.credits && (
                        <Form.Text className="text-danger">
                          {errors.credits.message}
                        </Form.Text>
                      )}
                    </Form.Group>
                  </Col>
                  <Col xs={12} lg={4}>
                    <Form.Group className="mb-3" controlId="formCourseGrade">
                      <Form.Label className="fw-bold">im(*)</Form.Label>
                      <Form.Select
                        className="border border-2"
                        {...register("grade")}
                      >
                        <option value="">Chn im</option>
                        {grades.map((grade) => (
                          <option key={grade.value} value={grade.value}>
                            {grade.label}
                          </option>
                        ))}
                      </Form.Select>
                      {errors.grade && (
                        <Form.Text className="text-danger">
                          {errors.grade.message}
                        </Form.Text>
                      )}
                    </Form.Group>
                  </Col>
                </Row>

                <Row>
                  <Col xs={12} md={12} className="text-center">
                    <StyledAddButton type="submit">
                      <Icon className="fs-5 my-1 fw-bold" icon="gala:add" />
                    </StyledAddButton>
                  </Col>
                </Row>
              </Form>
              {showCourseTable && (
                <div className="col-12 col-md-12 col-sm-12 mx-auto mt-3">
                  <Table
                    responsive
                    striped
                    bordered
                    hover
                    className="text-center"
                  >
                    <thead>
                      <tr className="align-middle">
                        <th width="40%">Mn hc</th>
                        <th width="25%">S tn ch</th>
                        <th width="25%">im</th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {courseList.map((course, index) => (
                        <tr key={index} className="align-middle">
                          <td>{course.name}</td>
                          <td>{course.credits}</td>
                          <td>
                            {course.grade}{" "}
                            {course.grade.length === 1 ? `\u00A0` : ""}
                          </td>
                          <td>
                            <Button
                              variant="danger"
                              onClick={() => deleteCourse(index)}
                              size="sm"
                            >
                              <Icon
                                className="fs-5"
                                icon="weui:delete-on-filled"
                              />
                            </Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              )}
              <div className="fs-5 fw-bold">{gpa}</div>
            </Card>
          </div>
        </Container>
      </main>
    </>
  );
};

export default GPAApp;
