import React from "react";
// Components
import { Col, Container, Row } from "react-bootstrap";
import Title from "../components/Title";
import AppCard from "../components/AppCard";

// #region component
const AllApps = () => {
  const appList = [
    {
      id: "1",
      image: "material-symbols-light:calculate-outline",
      name: "Tnh im tng kt",
      url: "/apps/FinalGrade",
    },
    {
      id: "2",
      image: "tabler:school",
      name: "Tnh im GPA",
      url: "/apps/GPA",
    },
  ];

  return (
    <>
      <main>
        <Container className="d-flex justify-content-center">
          <Title size={"h2"} text={"All Apps"} />
        </Container>
        <Container className="my-5 ">
          <Row
            xs={1}
            sm={2}
            md={2}
            lg={3}
            xxl={4}
            className="g-4 justify-content-center row"
          >
            {appList.map((element) => {
              return (
                <Col key={element.id}>
                  <AppCard
                    image={element.image}
                    name={element.name}
                    url={element.url}
                  />
                </Col>
              );
            })}
          </Row>
        </Container>
      </main>
    </>
  );
};
// #endregion

export default AllApps;
