import React, { useState, useEffect } from "react";
import { Container, Card, Form, Table, Button } from "react-bootstrap";
const FinalGradeApp = () => {
  const TheWeight = [
    [10, 20, 20, 50],
    [10, 10, 30, 50],
    [10, 10, 20, 60],
    [10, 10, 10, 70],
    [10, 30, 60],
    [10, 20, 70],
    [10, 10, 80],
  ];
  const [selected, setSelected] = useState(0);
  const [gradeInput, setGradeInput] = useState([0, 0, 0, 0]);
  const [finalGrade, setFinalGrade] = useState("");
  const [minGrades, setMinGrades] = useState({
    AP: "",
    A: "",
    BP: "",
    B: "",
    CP: "",
    C: "",
    DP: "",
    D: "",
  });
  const [showMinTable, setShowMinTable] = useState(true);
  const [showExchangeTable, setShowExchangeTable] = useState(false);

  const changeTheWeight = (event) => {
    const selectedIndex = event.target.value;
    setSelected(selectedIndex);
    setGradeInput([0, 0, 0, 0]);
  };

  const checkHasZero = (mode) => {
    // const normalizedGradeInput = gradeInput.map((value) =>
    //   value === "" ? 0 : parseFloat(value)
    // );
    console.log("gi checkzero");
    const newHasZero = gradeInput
      .slice(0, TheWeight[selected].length - mode)
      .some((input) => input === "" || input <= 0);
    console.log(gradeInput.slice(0, TheWeight[selected].length - mode));
    return newHasZero;
  };

  const calculateFinalGrade = () => {
    // console.log("gi final");
    // console.log(gradeInput);
    const newFinalGrade = TheWeight[selected].reduce(
      (acc, weight, idx) => acc + weight * gradeInput[idx],
      0
    );
    const roundedGrade = roundGrade(newFinalGrade / 100);
    setFinalGrade(roundedGrade);
  };

  const roundGrade = (finalGrade) => {
    const tmp = finalGrade.toFixed(2);
    const rounded = Math.round(finalGrade * 10) / 10;
    if (tmp > 10.0) return "";
    return `${tmp} ${getCharGrade(rounded)}`;
  };

  const getCharGrade = (finalGrade) => {
    if (checkHasZero(0)) return " (F)";
    if (finalGrade >= 9.0) return " (A+) ";
    if (finalGrade >= 8.5) return " (A)";
    if (finalGrade >= 8.0) return " (B+)";
    if (finalGrade >= 7.0) return " (B)";
    if (finalGrade >= 6.5) return " (C+)";
    if (finalGrade >= 5.5) return " (C)";
    if (finalGrade >= 5.0) return " (D+)";
    if (finalGrade >= 4.0) return " (D)";
    return " (F)";
  };

  const calculateMinimumFinalGrade = () => {
    // console.log("gi minimun");
    // console.log(gradeInput);
    const weight = TheWeight[selected];
    const currentGrade = weight
      .slice(0, -1)
      .reduce((acc, weight, idx) => acc + weight * gradeInput[idx], 0);
    const remainingWeight = weight.at(-1);

    const hasZero = checkHasZero(1);
    const newMinGrades = {
      AP: calculateRequiredGrade(remainingWeight, currentGrade, 8.95, hasZero),
      A: calculateRequiredGrade(remainingWeight, currentGrade, 8.45, hasZero),
      BP: calculateRequiredGrade(remainingWeight, currentGrade, 7.95, hasZero),
      B: calculateRequiredGrade(remainingWeight, currentGrade, 6.95, hasZero),
      CP: calculateRequiredGrade(remainingWeight, currentGrade, 6.45, hasZero),
      C: calculateRequiredGrade(remainingWeight, currentGrade, 5.45, hasZero),
      DP: calculateRequiredGrade(remainingWeight, currentGrade, 4.95, hasZero),
      D: calculateRequiredGrade(remainingWeight, currentGrade, 3.95, hasZero),
    };

    setMinGrades(newMinGrades);
  };

  const calculateRequiredGrade = (
    remainingWeight,
    currentGrade,
    targetGrade,
    hasZero
  ) => {
    if (hasZero) return "Khng th t";
    const requiredGrade = (targetGrade * 100 - currentGrade) / remainingWeight;
    return requiredGrade > 10 ? "Khng th t" : requiredGrade.toFixed(2);
  };

  const toggleTable = (table) => {
    if (table === "min") {
      setShowMinTable(!showMinTable);
    } else {
      setShowExchangeTable(!showExchangeTable);
    }
  };

  const handleInputChange = (index, value) => {
    const numericValue = parseFloat(value);
    const newGradeInput = [...gradeInput];
    newGradeInput[index] = isNaN(numericValue) ? "" : numericValue;
    setGradeInput(newGradeInput);
  };

  useEffect(() => {
    calculateFinalGrade();
    calculateMinimumFinalGrade();
  }, [gradeInput]);

  const exchangeTableData = [
    { scale10: "9.0 - 10.0", scale4: "4.0", letter: "A+" },
    { scale10: "8.5 - 8.9", scale4: "3.7", letter: "A" },
    { scale10: "8.0 - 8.4", scale4: "3.5", letter: "B+" },
    { scale10: "7.0 - 7.9", scale4: "3.0", letter: "B" },
    { scale10: "6.5 - 6.9", scale4: "2.5", letter: "C+" },
    { scale10: "5.5 - 6.4", scale4: "2.0", letter: "C" },
    { scale10: "5.0 - 5.4", scale4: "1.5", letter: "D+" },
    { scale10: "4.0 - 4.9", scale4: "1.0", letter: "D" },
    { scale10: "0 - 3.9", scale4: "0.0", letter: "F" },
  ];

  return (
    <>
      <main>
        <Container className="d-flex justify-content-center"></Container>
        <Container className="my-5">
          <div className="col col-md-6 col-sm-12 mx-auto">
            <Card className="p-3 shadow">
              <Card.Title className="text-center">
                Tnh im Tng Kt
              </Card.Title>
              <Form.Group>
                <Form.Label className="fw-bold">Chn trng s</Form.Label>
                <Form.Select
                  defaultValue={selected}
                  onChange={changeTheWeight}
                  className="border border-2 mw-100"
                >
                  {TheWeight.map((weight, index) => (
                    <option value={index} key={index}>
                      {weight.join("-")}
                    </option>
                  ))}
                </Form.Select>
              </Form.Group>

              <Table
                responsive
                striped
                bordered
                hover
                className="mt-3 text-center"
              >
                <thead>
                  <tr>
                    {TheWeight[selected].map((weight, index) => (
                      <th key={index}>im {weight}%</th>
                    ))}
                    <th
                      className="align-middle"
                      width={`${100 / (TheWeight[selected].length + 1)}%`}
                    >
                      Tng kt
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    {TheWeight[selected].map((weight, index) => (
                      <td className="align-middle" key={index}>
                        <Form.Control
                          type="number"
                          min="0"
                          max="10"
                          step="any"
                          value={gradeInput[index]}
                          onChange={(e) =>
                            handleInputChange(index, e.target.value)
                          }
                        />
                      </td>
                    ))}
                    <td className="fw-bold align-middle">{finalGrade}</td>
                  </tr>
                </tbody>
              </Table>
              <div className="d-flex justify-content-around my-1 w-75 mx-auto">
                <Button variant="link" onClick={() => toggleTable("min")}>
                  im ti thiu
                </Button>
                <Button variant="link" onClick={() => toggleTable("exchange")}>
                  Bng quy i
                </Button>
              </div>
              {showMinTable && (
                <div className="col-12 col-md-9 col-sm-12 mx-auto">
                  <Table striped bordered hover className="text-center">
                    <thead>
                      <tr>
                        <th className="align-middle" width="50%">
                          im ch
                        </th>
                        <th>im cui k ti thiu</th>
                      </tr>
                    </thead>
                    <tbody>
                      {Object.keys(minGrades).map((key, index) => (
                        <tr key={index}>
                          <td className="fw-bold">
                            {key.includes("P")
                              ? key.replace("P", "+")
                              : key + "\u00A0\u00A0"}
                          </td>
                          <td>{minGrades[key]}</td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              )}
              {showExchangeTable && (
                <div className="col-12 col-md-9 col-sm-12 mx-auto">
                  <Table striped bordered hover className="text-center">
                    <thead>
                      <tr>
                        <th className="align-middle">H 10</th>
                        <th className="align-middle" width="33.3%">
                          H 4
                        </th>
                        <th width="33.3%">im ch</th>
                      </tr>
                    </thead>
                    <tbody>
                      {exchangeTableData.map((row, index) => (
                        <tr key={index}>
                          <td>{row.scale10}</td>
                          <td>{row.scale4}</td>
                          <td>
                            {row.letter}
                            {row.letter.length === 1 ? `\u00A0\u00A0` : ""}
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              )}
            </Card>
          </div>
        </Container>
      </main>
    </>
  );
};

export default FinalGradeApp;
